<template>
  <div class = "sing-show">
     <second-header></second-header>
     <second-middle></second-middle>
     <second-footer></second-footer>
  </div>  
</template>
<script>
import SecondHeader from "@/views/second/SecondHeader"
import SecondMiddle from "@/views/second/SecondMiddle"
import SecondFooter from "@/views/second/SecondFooter"
export default {
  components:{
    SecondHeader,
    SecondMiddle,
    SecondFooter
  }
}
</script>
<style scoped>
  .sing-show{
    height:100%;
    overflow: hidden;
  }
</style>



