import Vue from 'vue'
import Router from 'vue-router'
import First from '@/views/first/first'
import Second from '@/views/second/second'
Vue.use(Router)

export default new Router({  
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'First',
      component: First
    },
    {
      path: '/play',
      name: 'Second',
      component: Second
    }
  ]
})



